import { supabase } from "./supabaseClient";
import { UpdateResult } from "./type";

export const sendRowsToSupabase = async (): Promise<UpdateResult> => {
    try {
        return await Excel.run(async (context) => {
            const sheet = context.workbook.worksheets.getActiveWorksheet();

            // Check used range (sheet empty)
            const usedRangeOrNull = sheet.getUsedRangeOrNullObject();
            usedRangeOrNull.load(["isNullObject", "values", "rowCount", "columnCount"]);
            await context.sync();

            const delay = (ms: number) => new Promise(resolve => setTimeout(resolve, ms));

            const isSheetEmpty =
                usedRangeOrNull.isNullObject ||
                usedRangeOrNull.values.flat().every(
                    v => v === null || v === undefined || v === ""
                );

            if (isSheetEmpty) {
                await delay(2000);
                return {
                    type: "info",
                    message: "The Excel sheet is empty. Please add data before syncing."
                };
            }

            const selectedRanges = context.workbook.getSelectedRanges();
            selectedRanges.load("areas/items");
            await context.sync();

            const usedRange = sheet.getUsedRange();
            usedRange.load(["values", "columnCount"]);
            await context.sync();

            const headers: string[] = usedRange.values[0]?.map((h: any) =>
                (h ?? "").toString().trim()
            );

            if (!headers || headers.length === 0) {
                await delay(2000);
                return {
                    type: "info",
                    message: "Could not detect any column headers."
                };
            }

            const columnCount = usedRange.columnCount;

            // Validate selected rows contain data
            let hasValidSelectedData = false;

            for (const area of selectedRanges.areas.items) {
                area.load(["rowIndex", "rowCount"]);
                await context.sync();

                for (let i = 0; i < area.rowCount; i++) {
                    const rowIndex = area.rowIndex + i;

                    // Skip header
                    if (rowIndex === 0) continue;

                    const rowRange = sheet.getRangeByIndexes(rowIndex, 0, 1, columnCount);
                    rowRange.load("values");
                    await context.sync();

                    const rowValues = rowRange.values[0];
                    if (rowValues.some(v => v !== null && v !== undefined && v !== "")) {
                        hasValidSelectedData = true;
                        break;
                    }
                }

                if (hasValidSelectedData) break;
            }

            if (!hasValidSelectedData) {
                await delay(2000);
                return {
                    type: "info",
                    message: "Selected row(s) are empty or contain no data."
                };
            }

            // Collect selected rows
            const allRowsData: Record<string, any>[] = [];

            for (const area of selectedRanges.areas.items) {
                for (let i = 0; i < area.rowCount; i++) {
                    const rowIndex = area.rowIndex + i;

                    // Skip header
                    if (rowIndex === 0) continue;

                    const rowRange = sheet.getRangeByIndexes(rowIndex, 0, 1, columnCount);
                    rowRange.load("values");
                    await context.sync();

                    const rowValues = rowRange.values[0];
                    const rowObject: Record<string, any> = {};

                    headers.forEach((header, idx) => {
                        rowObject[header] = rowValues[idx] ?? null;
                    });

                    allRowsData.push(rowObject);
                }
            }

            if (allRowsData.length === 0) {
                await delay(2000);
                return {
                    type: "info",
                    message: "No data rows found in the selection."
                };
            }

            const payload = allRowsData
                .filter(
                    row =>
                        row["Kaizen ID #"] &&
                        row["Kaizen ID #"].toString().trim() !== ""
                )
                .map(row => ({
                    id: row["Kaizen ID #"],
                    kaizen: row
                }));

            if (payload.length === 0) {
                return {
                    type: "info",
                    message: "No rows with a valid Kaizen ID found."
                };
            }

            // Upsert to Supabase
            const { error } = await supabase
                .from("kaizens")
                .upsert(payload, { onConflict: "id" });

            if (error) throw error;

            return {
                type: "success",
                message: `${payload.length} row(s) successfully upserted to Supabase.`
            };
        });
    } catch (err: any) {
        console.error("Supabase insert error:", err);
        return {
            type: "error",
            message: err?.message || "Unexpected error occurred."
        };
    }
};
