let dialogRef: Office.Dialog | null = null;

export const openLoaderDialog = (): Promise<Office.Dialog> => {
    return new Promise((resolve, reject) => {
        // If already opened, reuse
        if (dialogRef) {
            resolve(dialogRef);
            return;
        }

        Office.context.ui.displayDialogAsync(
            "https://localhost:3000/assets/dialog.html",
            { height: 30, width: 40 },
            (result) => {
                if (result.status === Office.AsyncResultStatus.Succeeded) {
                    dialogRef = result.value;

                    // Listen msg from dialog
                    dialogRef.addEventHandler(
                        Office.EventType.DialogMessageReceived,
                        (arg) => {
                            try {
                                if ("message" in arg) {
                                    const msg = JSON.parse(arg.message);

                                    if (msg.type === "CLOSE_DIALOG") {
                                        closeDialog();
                                    }
                                };
                            } catch {
                                console.warn("Invalid dialog message:", "message" in arg ? arg.message : "unknown error");
                            };
                        }
                    );
                    resolve(dialogRef);
                } else {
                    reject(result.error);
                }
            }
        );
    });
};

export const sendDialogMessage = (type: "success" | "error" | "info", message: string) => {
    if (dialogRef) {
        dialogRef.messageChild(JSON.stringify({ type, message }));
    } else {
        console.warn("Dialog not ready to send message:", type, message);
    }
};

export const closeDialog = () => {
    if (dialogRef) {
        dialogRef.close();
        dialogRef = null;
    }
};
