import { supabase } from "../taskpane/utilities/supabaseClient";
import { openLoaderDialog, sendDialogMessage } from "../taskpane/utilities/loaderDialog";
import { handleUpdateSelectedRowsFromSupabase } from "../taskpane/utilities/updateSelectedRowsFromSupabase";
import { sendRowsToSupabase } from "../taskpane/utilities/supabaseInsert";

/* global Office */
Office.onReady(() => {
  // If needed, Office.js is ready to be called.
});

/**
 * Shows a notification when the add-in command is executed.
 * @param event
 */

async function SendToSupabaseButton(event: Office.AddinCommands.Event) {
  try {
    // Open loader
    await openLoaderDialog();

    // Insert rows & get result
    const result = await sendRowsToSupabase();

    // Send result message to dialog
    sendDialogMessage(result.type, result.message);
  } catch (err: any) {
    console.error(err);
    sendDialogMessage("error", err?.message || "Unexpected error occurred.");
  } finally {
    event.completed();
  };
};

// Register the function with Office
Office.actions.associate("SendToSupabaseButton", SendToSupabaseButton);

async function GetFromSupabaseButton(event: Office.AddinCommands.Event) {
  try {
    // Open loader ONCE
    await openLoaderDialog();

    const { data, error } = await supabase
      .from("kaizens")
      .select("*");

    if (error) throw error;
    if (!data || data.length === 0) {
      sendDialogMessage("info", "No Kaizen data found.");
      return;
    };

    const rows = data
      .map((r) => r.kaizen)
      .filter((k) => k && k["Kaizen ID #"]);

    if (rows.length === 0) {
      sendDialogMessage("info", "No valid Kaizen rows found.");
      return;
    };

    const updateResult = await handleUpdateSelectedRowsFromSupabase(rows);

    // Send professional message back to dialog
    sendDialogMessage(updateResult.type, updateResult.message);
  } catch (err: any) {
    console.error(err);
    sendDialogMessage(
      "error",
      err?.message || "Unexpected error occurred."
    );
  } finally {
    event.completed();
  }
};

// Register the function with Office
Office.actions.associate("GetFromSupabaseButton", GetFromSupabaseButton);