import { FIXED_HEADERS } from "./headers";
import { UpdateResult } from "./type";

export const handleUpdateSelectedRowsFromSupabase = async (incomingKaizens: any[]): Promise<UpdateResult> => {
    try {
        return await Excel.run(async (context) => {
            const sheet = context.workbook.worksheets.getActiveWorksheet();

            const cleanValue = (value: any) => {
                if (value === null || value === undefined) return "";
                return value.toString().replace(/[\n\r\t]+/g, " ").replace(/\s+/g, " ").trim();
            };

            let usedRange = sheet.getUsedRangeOrNullObject();
            usedRange.load(["isNullObject", "values", "columnCount", "rowCount"]);
            await context.sync();

            let isSheetEmpty = false;
            if (usedRange.isNullObject) {
                isSheetEmpty = true;
            } else {
                const flatValues = usedRange.values.flat();
                if (flatValues.every(v => v === null || v === undefined || v === "")) {
                    isSheetEmpty = true;
                }
            }

            const updatedRows: string[] = [];
            const skippedRows: string[] = [];

            if (isSheetEmpty) {
                const rows = incomingKaizens.map(k =>
                    FIXED_HEADERS.map(h => cleanValue(k[h]))
                );
                const table = [FIXED_HEADERS, ...rows];
                const range = sheet.getRangeByIndexes(0, 0, table.length, FIXED_HEADERS.length);
                range.values = table;
                range.getRow(0).format.font.bold = true;
                sheet.getUsedRange().format.autofitColumns();
                sheet.getUsedRange().format.autofitRows();
                await context.sync();

                return {
                    type: "success",
                    message: `Sheet was empty. Inserted ${rows.length} Kaizen row(s) using standard headers.`
                };
            }

            // Sheet has data, update only selected rows
            const selectedRanges = context.workbook.getSelectedRanges();
            selectedRanges.load("areas");
            await context.sync();

            if (selectedRanges.areas.items.length === 0) {
                return {
                    type: "info",
                    message: "No rows selected for update."
                };
            };

            // Load headers
            usedRange = sheet.getUsedRange();
            usedRange.load("columnCount, values");
            await context.sync();
            const headers: string[] = usedRange.values[0].map((h: any) => cleanValue(h ?? ""));
            const columnCount = usedRange.columnCount;

            for (const area of selectedRanges.areas.items) {
                area.load(["rowIndex", "rowCount"]);
                await context.sync();

                const areaRange = sheet.getRangeByIndexes(area.rowIndex, 0, area.rowCount, columnCount);
                areaRange.load("values");
                await context.sync();
                const areaValues = areaRange.values;

                for (let i = 0; i < area.rowCount; i++) {
                    const rowValues = areaValues[i];
                    const idColIndex = headers.indexOf("Kaizen ID #");
                    const kaizenId = rowValues[idColIndex];

                    const incomingKaizen = incomingKaizens.find(k => k["Kaizen ID #"] === kaizenId);
                    if (!incomingKaizen) {
                        skippedRows.push(kaizenId ?? `Row ${area.rowIndex + i + 1}`);
                        continue;
                    }

                    const newRowValues = headers.map(header =>
                        incomingKaizen.hasOwnProperty(header) ? cleanValue(incomingKaizen[header]) : rowValues[headers.indexOf(header)]
                    );

                    const rowRange = sheet.getRangeByIndexes(area.rowIndex + i, 0, 1, headers.length);
                    rowRange.values = [newRowValues];
                    updatedRows.push(kaizenId);
                }
            }

            // Auto-fit
            sheet.getUsedRange().format.autofitColumns();
            sheet.getUsedRange().format.autofitRows();
            await context.sync();

            let message = "";
            if (updatedRows.length > 0) {
                message += `Updated ${updatedRows.length} row(s): ${updatedRows.join(", ")}.\n`;
            } else if (skippedRows.length > 0) {
                message += `No rows were updated. Please select at least one row to update.`;
            };

            return {
                type: updatedRows.length > 0 ? "success" : "info",
                message: message || "No changes were made."
            };
        });
    } catch (error) {
        console.error("Error updating selected rows:", error);
        return {
            type: "error",
            message: `Failed to update rows: ${error?.message || error}`
        };
    }
};